/* purgecss start ignore */

.homepage{
  height:min(-webkit-min-content,80vh);
  height:min(min-content,80vh);
  align-items: center;
}

.play{
  top:40%;
  left:50%;
  cursor: pointer;
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
}

.playIcon{
  color:white;
  background-color: rgba(0,0,0,0.4);
  border-radius: 50%;
  padding:10px;
}

.playIcon:hover{
  background-color: rgba(0,0,0,0.6);
}

/* purgecss end ignore */
/* purgecss start ignore */

.Navbar ul>.active,ul>.item:hover{
  background-color: rgb(191, 69, 69);
  border-radius: 999px;
  color:white;
  padding:0 15px;
  font-size: smaller;
  display: flex;
  align-items: center;
  text-align: center;
}

.Navbar{
  z-index: 200;
  position:fixed;
  width:100vw;
  display: flex;
  padding:20px 20px 20px 20px;
  background-color:#f1f6ff;
}

.Navbar ul>li{
  text-align: center;
  margin-right:20px;
  font-weight: 700;
  padding:0 7px;
  cursor: pointer;
}

.translate{
  display: flex;
  align-items: center;
  font-weight: 500;
}

.translate>img{
  width:13px;
  height:23px;
  object-fit: contain;
  margin-right:10px;
}

.hiddenMenu{
  position:absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  width:100vw;
  transition:all 0.3s ease-in-out;
  height:100vh;
  width: 100vw;
}

.activemenu{
  -webkit-transform: translateY(0);
  transform: translateY(0);
}

.inactivemenu{
  -webkit-transform: translateY(-100vh);
  transform: translateY(-100vh);
}

.icons:hover .hide{
  display: inline;
}

/* purgecss end ignore */
/* purgecss start ignore */

.icons svg{
  color:white;
  background-color: black;
  padding:2px;
  border-radius: 10px;
}

.sidebar{
  position: fixed;
  z-index: 100;
  top:30%;
  left:2%;
}

.sidebar .active{
  background-color: aqua;
}

